import React from "react";
import { faServicesApiInstance } from "@faharmony/service";
import { FAHarmonySVGPath } from "@faharmony/icons";
import { IFABarAppButtonProps } from "./types";

const harmonyApp: IFABarAppButtonProps = {
  position: 0,
  id: "fa-harmony",
  appName: "FA Harmony",
  iconPath: FAHarmonySVGPath,
  url: "/",
  openInNewWindow: false,
};

export default (devMode: boolean = false): IFABarAppButtonProps[] => {
  const [data, setData] = React.useState<IFABarAppButtonProps[]>([]);
  React.useEffect(() => {
    faServicesApiInstance.get("navigator/").then(({ data }) => {
      if (data && Array.isArray(data)) setData(data);
    });
  }, []);
  return devMode
    ? [harmonyApp]
    : data.sort(({ position: a }, { position: b }) => a - b);
};
