import React, { Suspense } from "react";
import ReactDOM from "react-dom";
import {
  Keycloak,
  authenticateWithKeycloak,
  KeycloakContext,
  KeycloakInstance,
  GraphQLProvider,
} from "@faharmony/service";
import { HashRouter as Router } from "@faharmony/router";
import { ToastProvider, Text } from "@faharmony/components";
import { StateProvider, Slice } from "@faharmony/state";
import { Div100vh } from "@faharmony/views";

import Loader from "./App/Loader";
import App, { AppContext } from "./App";
import { IRenderProps } from "./types";

const getModuleSlices = (Modules: IRenderProps["Modules"]) =>
  Modules.reduce<Slice[]>((acc, m) => {
    if (m?.state) acc.push(m.state);
    return acc;
  }, []);

/**
 * renderOnSuccess
 * ---
 * Generates the react component that should be
 * rendered when authentication is successful.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
const renderOnSuccess = ({
  keycloakInstance,
  args,
}: {
  keycloakInstance: KeycloakInstance;
  args: IRenderProps;
}) => {
  const appConfig = args.appConfig;
  return (
    <Suspense fallback={<Loader />}>
      <Router>
        <StateProvider slices={getModuleSlices(args.Modules)}>
          <AppContext.Provider value={appConfig}>
            <KeycloakContext.Provider value={keycloakInstance}>
              <ToastProvider>
                <GraphQLProvider keycloakInstance={keycloakInstance}>
                  <Div100vh id="full-height-wrapper">
                    <App {...args} />
                  </Div100vh>
                </GraphQLProvider>
              </ToastProvider>
            </KeycloakContext.Provider>
          </AppContext.Provider>
        </StateProvider>
      </Router>
    </Suspense>
  );
};

/**
 * renderOnFailure
 * ---
 * Generates the react component that should be
 * rendered when authentication has failed.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
const renderOnFailure = () => {
  return (
    <Suspense fallback={<Loader />}>
      <div>
        <Text variant="h1">Authentication Failed</Text>
      </div>
    </Suspense>
  );
};

/**
 * render()
 * ---
 * The render function combines multiple third party libraries which are constant among all react apps.
 * It renders the App onto the DOM while maintaining Keycloak authentication and constructing FA Global Bar.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
const render = (props: IRenderProps) => {
  const { keycloakConfig, devMode = false, _harmony = false } = props;

  const onAuthSuccess = (
    keycloakInstance: KeycloakInstance,
    args: IRenderProps = props
  ) => {
    ReactDOM.render(
      renderOnSuccess({ keycloakInstance, args }),
      document.getElementById("root")
    );
  };

  const onAuthFailure = () => {
    ReactDOM.render(renderOnFailure(), document.getElementById("root"));
  };

  // DEV MODE
  if ((process.env.NODE_ENV === "development" && devMode) || _harmony) {
    const dummyKeycloak = Keycloak();
    dummyKeycloak.realmAccess = { roles: ["FA_ADMIN"] };
    dummyKeycloak.authenticated = true;
    dummyKeycloak.subject = "devMode";
    dummyKeycloak.token = "devMode";
    dummyKeycloak.realm = "fa";
    dummyKeycloak.clientId = "fa-harmony";
    onAuthSuccess(dummyKeycloak, props);
  }
  // PROD MODE
  else
    authenticateWithKeycloak({ keycloakConfig, onAuthSuccess, onAuthFailure });
};

export { render };
