import React from "react";
import { mergeRefs } from "@faharmony/helpers";
import { getFormattedNumeral, IFormatNumeral } from "@faharmony/locale";
import { InputField, IInputFieldProps } from "../BaseInputField";

interface INumberFieldProps extends IInputFieldProps, IFormatNumeral {}

/**
 * NumberField
 * ---
 * Component to manage/format input for numeric value
 * @jira https://fasolutions.jira.com/browse/HAR-76
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <NumberField value="999" />
 */
const NumberField = React.forwardRef<HTMLInputElement, INumberFieldProps>(
  (props, ref) => {
    const { placeholder, locale, format, ...inputProps } = props;

    const inputRef = React.useRef<HTMLInputElement>(null);
    React.useEffect(() => {
      if (inputRef && inputRef.current) {
        const input = inputRef.current;
        const update = () => {
          input.value =
            getFormattedNumeral(input.value, { locale, format }) || "";
        };
        update();
        input.onblur = update;
      }
    }, [inputRef]);

    return (
      <InputField
        {...inputProps}
        ref={mergeRefs([inputRef, ref])}
        textAlign="right"
        inputMode="decimal"
        placeholder={placeholder || format || "0.00"}
      />
    );
  }
);

export { NumberField, INumberFieldProps };
