import { default as numeral } from "numeral";

export type NumeralLocaleType = "en" | "fi";

export interface IFormatNumeral {
  /** Format string from http://numeraljs.com @default "0,0.00" */
  format?: string;
  /** One of https://github.com/adamwdraper/Numeral-js/tree/master/locales
   * Provide file-name without extension. @default "en" */
  locale?: NumeralLocaleType;
}

const loadNumeralLocale = (locale: string) => {
  if (locale !== "en") {
    try {
      require(`numeral/locales/${locale}`);
      numeral.locale(locale);
    } catch {
      console.warn`Couldn't find locale '${locale}'.`;
    }
  }
  return numeral;
};

export const getFormattedNumeral = (
  value: any,
  { format = "0,0.00", locale = "en" }: IFormatNumeral
) => loadNumeralLocale(locale)(value).format(format);

export const getNumeralValue = (value: any) => numeral(value).value();

export { numeral };
